* {
  box-sizing: border-box;
}

html,body {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  user-select: none;
}

html {
  background-color: #00b9c2;
  color: #fff;
  font-size: 16px;
}

input, button, select {
  appearance: none;
  display: block;
  min-width: 0;
  box-sizing: border-box;
  border-style: solid;
  border-width: 1px;
  border-color: rgba(225,225,225,0.5);
  box-shadow: none;
  outline: none;
  font-size: inherit;
  height: 2.5em;
}

pre {
  word-break: break-all;
  white-space: pre-wrap;
}


.card-view {
  & + .card-view {
    margin-top: 32px;
  }
  .card-view-content {
    border-radius: 16px;
    background: rgba(255, 255, 255, 0.2);
    overflow: hidden;
  }
}
.card-view-title {
  margin-bottom: 4px;
  font-size: 0.8em;
  margin-left: 16px;
  opacity: 0.8;
}
